
import { defineComponent } from "vue";

export default defineComponent({
  data() {
    return {
      opened: false,
    };
  },

  methods: {
    toggle() {
      this.opened = !this.opened;
    },
    start(el: HTMLDivElement) {
      el.style.height = `${el.scrollHeight}px`;
    },
    end(el: HTMLDivElement) {
      el.style.height = "";
    },
  },
});
